<template>
    <div class="inputdidi">
      <div class="wrap">
        <h1>{{label}} :</h1>
        <input :type="type" v-model="currentvalue"/>
      </div>

    </div>
</template>

<script>
    export default {
        name: "inputdidi",
        data () {
              return {
                currentvalue:this.value
              }
        },
        props:{
            label:{
              type:String
            },
          value:{

          },
          type:{
            type:String
          }
      },
      watch: {
        value (val) {
          this.currentvalue = val;
        },
        currentvalue(val){
          this.$emit("input",val)
        }
      }
    }
</script>

<style scoped>
      .wrap {
        width: 305px;
        margin: 0px auto;
        height: 40px;
        line-height: 40px;
        display: flex;
        border: 2px solid #c3c3c3;
        border-radius:8px;
        background: #fff;
      }
      .wrap input{
        border: none;
        outline: none;
        width: 200px;
      }
      .wrap h1{
        font-weight: 700;
      }
</style>



