<template>
  <div class="didi">
    <mt-header title="登陆" id="header">
      <router-link to="/" slot="left" >
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
      <div class="icon">
        <i class="iconfont icon-user" ></i>
      </div>
        <inputdidi label="用户名" type="text" v-model="Fred"></inputdidi>
        <inputdidi label="密码" type="password" v-model="Flintstone"></inputdidi>
    <div class="login">

      <mt-button type="primary" class="btn" @click="denglu">登陆</mt-button>
      <mt-button type="default" class="btn" @click="zhuce">注册</mt-button>
    </div>


  </div>
</template>

<script>
  import axios from "axios"
  import inputdidi from "./inputdidi.vue"

    export default {
        name: "mydidi",
        data:function () {
          return {
            Fred:'',
            Flintstone:''
          }
        },
      methods:{

          denglu:function () {
            console.log(222)

            let that = this
            axios.post('http://localhost:3000/api/user/login', {
              phone:this.Fred,
              pwd:this.Flintstone,
            },{withCredentials:true})
              .then(function (response) {
                console.log(response);
                  that.isdenglu()
              })
              .catch(function (error) {
                console.log(error);
              });
          },
        zhuce:function () {
              this.$router.push('/login')
        },
        isdenglu(){

          let that = this
          console.log(11111);
          axios.get('http://localhost:3000/api/user/isLogin',{withCredentials:true})
            .then(function (response) {
              console.log(response);
              console.log(response.data.ret);
              if(response.data.ret){
                that.$router.push('/gerenmsg')

              }else{

                that.$router.push('/mydidi')
              }
            })
            .catch(function (error) {
              console.log(error);
            });

        }
      },
        components:{
          inputdidi
        },
        mounted(){
          console.log(111)
          // setTimeout(this.isdenglu,3000)
           this.isdenglu();
          // setInterval(this.isdenglu(),1000)
          // let that = this
          // // console.log(11111);
          // axios.get('http://didi.yaojunrong.com/api/user/isLogin',{withCredentials: true})
          // .then(function (response) {
          //   if(!response.data.ret){
          //       that.$router.push('/mydidi')
          //   }else{
          //     that.$router.push('/gerenmsg')
          //   }
          // })
          // .catch(function (error) {
          //   console.log(error);
          // });
        }
    }

</script>

<style scoped>
  .didi #header{
    height: 45px;
    background: #006ed8;
  }
  .didi img{
    width: 100%;
    height: 151px;
    display: block;
  }
  .didi .dengji{
    width: 100%;
    display: block;
    height: 440px;
  }
  .didi .icon{
    width: 150px;
    height: 150px;
    background: white;
    border-radius:50%;
    line-height: 150px;
    margin: 40px auto 0;
    text-align: center;
  }
  .didi i{
    color: #c4cbd6;
    font-size: 100px;

  }
  .login{
    width: 305px;
    margin: 0 auto;
  }
  .btn{
    margin-top: 20px;
    display: block;
    width: 100%;
  }
</style>



